
















export default {
	data() {
		return {
			news:[],
		}
	},
	onLoad:function(){
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				this.news = res.data;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		enterInfo(e){
			var newsid = e.currentTarget.dataset.newid;
			uni.navigateTo({
				url: '../info/info?newid='+newsid,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
	}
}



